body {
  background-color: #e7ecf2;
}

ul {
  list-style: none;
  padding: 0;
  margin: 0;
}
.profile {
  display: block;
  width: 300px;
  margin: 60px auto;
  padding-top: 50px;
  background-color: #fff;
  border-radius: 4px;
  border: solid 1px rgb(212, 209, 209);
}

.description {
  display: block;
  margin: 0 auto 20px;
  width: 150px;
  text-align: center;
}
.avatar {
  width: 100px;
}

.name {
  font-size: 20px;
  font-weight: 700;
}
.tag {
  font-size: 14px;
  color: rgb(138, 136, 136);
}

.location {
  font-size: 14px;
  color: rgb(138, 136, 136);
}

.stats {
  display: flex;
  justify-content: space-between;
  border-top: solid 1px rgb(212, 209, 209);
  background-color: #f3f6f9;
}

.stats li {
  width: 100%;
  text-align: center;
  padding: 15px 0;
}
.stats li:not(:last-child) {
  border-right: solid 1px rgb(212, 209, 209);
}

.label {
  display: inline-block;
  text-align: center;
  margin: 0 auto;
  text-transform: capitalize;
  font-size: 12px;
  color: rgb(138, 136, 136);
}

.quantity {
  display: block;
  margin: 0 auto;
  font-size: 14px;
  font-weight: 700;
}
